/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import Select from "react-select"
import { useShopDispatch } from "../../shop/_state/hooks"
import { sanitizeLanguages } from "Api"

function TranslatorLanguages({ translatorState, translatorDispatch }) {
  const shopDispatch = useShopDispatch()

  const TranslatorCSS = css`
    width: 250px;
    max-width: 100%;
    margin: 0 5px;
  `

  function onChange(lang) {
    if (lang) {
      translatorDispatch({
        type: "SET_SELECTED_LANGUAGE",
        payload: lang,
      })

      shopDispatch({
        type: "SET_LANGUAGE",
        payload: lang.value.isoCode,
      })

      // shopDispatch({
      // 	type: 'SET_IS_FETCHING_NEW',
      // 	payload: true,
      // })
    }
  }

  return (
    <Select
      css={TranslatorCSS}
      onChange={onChange}
      options={sanitizeLanguages(
        translatorState.selectedCountry.value.availableLanguages
      )}
      value={translatorState.selectedLanguage?.value?.name}
      placeholder={wp.i18n.__("Select language ...", "shop")}
    />
  )
}

export default TranslatorLanguages
